.App{
  padding: 30px 15px;
}
.label{
  width: 100%;
  height: 50px;
  margin: 0 0 29px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--main-white);
  font-weight: 700;
  background: var(--main-green);
  border-radius: 10px;
  cursor: pointer;
}
.input{
  display: none;
}
.image-wrapper{
  width: 100%;
  height: calc(100vw - 30px);
  max-height: 444px;
  margin: 0 0 46px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--main-grey);
  border-radius: 10px;
  overflow: hidden;
}
.image{
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.buttons{
  width: 258px;
  margin: 0 auto;
  padding: 0;
  display: flex;
  justify-content: space-between;
}
.button{
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--main-grey);
  border-radius: 10px;
  transition: background 0.2s;
}
.button path{
  transition: fill 0.2s;
}
.button:hover path{
  fill: var(--main-green)
}
.button-wrapper:active > .button{
  background: var(--main-green);
}
.button:active path{
  fill: var(--main-white)
}
.button:focus{
  background: var(--main-focus-grey);
}
.button.active{
  background: var(--main-green);
}
.button.active path{
  fill: var(--main-white);
}